import React from 'react';
import styles from './LiveLeaderboardTable.module.css';

type LeaderboardPlayer = {
  position: string;
  player: string;
  score: number;
  thru: string;
  chosenCount: number;
  rounds: { strokes: number }[];
};

type LiveLeaderboardTableProps = {
  leaderboard: LeaderboardPlayer[];
};

const LiveLeaderboardTable: React.FC<LiveLeaderboardTableProps> = ({ leaderboard }) => {
  return (
    <table className={styles.table}>
      <thead>
        <tr>
          <th>Position</th>
          <th>Player</th>
          <th>Score</th>
          <th>Thru</th>
          <th># Pick</th>
          <th>R1</th>
          <th>R2</th>
          <th>R3</th>
          <th>R4</th>
        </tr>
      </thead>
      <tbody>
        {leaderboard.map((player, index) => (
          <tr key={index}>
            <td>{player.position}</td>
            <td>{player.player}</td>
            <td>{player.score}</td>
            <td>{player.thru}</td>
            <td>{player.chosenCount || ''}</td>
            {player.rounds.map((round, roundIndex) => (
              <td key={roundIndex}>{round.strokes}</td>
            ))}
          </tr>
        ))}
      </tbody>
    </table>
  );
};

export default LiveLeaderboardTable;